<template>
  <el-form
    class="ling-camera_explore"
    label-position="right"
    label-width="100px"
    :model="cameraExplore"
    v-loading="loading"
    ref="exploreForm">
    <p class="tips">根据环境实际情况，设置摄像头曝光值，范围-3到3，数值小则暗，数值大则亮。</p>
    <el-form-item label="曝光参数：">
      <el-input type="number" placeholder="请输入曝光参数" v-model="cameraExplore.camera_explore"></el-input>
    </el-form-item>

    <el-form-item>
      <el-button type="primary" @click="handleSave('exploreForm')">保存</el-button>
    </el-form-item>
  </el-form>
</template>

<script>
import getSettingInfo from '@/mixins/getSettingInfo'
import { mapActions } from 'vuex'
export default {
  name: 'LingCameraExplore',
  mixins: [getSettingInfo],
  data () {
    return {
      loading: false,
      cameraExplore: {
        camera_explore: ''
      }
    }
  },
  watch: {
    getSettingInfo: {
      handler (val) {
        this.cameraExplore.camera_explore = +val.camera_explore
      },
      immediate: true,
      deep: true
    }
  },
  methods: {
    ...mapActions({
      setCameraExplore: 'setCameraExplore'
    }),
    handleSave (formName) {
      if (this.loading) {
        return
      }

      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          this.loading = true
          try {
            await this.setCameraExplore(this.cameraExplore)
          } catch (res) {}

          this.loading = false
        } else {
          console.log('error submit!!')
          return false
        }
      })
    }
  }
}
</script>

<style lang="scss">
.ling-temperature-threshold {
  .el-form-item {
    .el-input__inner {
      font: {
        size: 16px;
        weight: bold;
      }
    }
  }
}
</style>



